import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ApiProvider } from '../../providers/api/api';
import { LoadingController } from 'ionic-angular';
import { Events } from 'ionic-angular';
/**
 * Generated class for the EquipamentoPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-equipamento',
  templateUrl: 'equipamento.html',
})
export class EquipamentoPage {

  constructor(public navCtrl: NavController, public navParams: NavParams, public apiProvider: ApiProvider, public loadingController:LoadingController, public events: Events){


  this.dataEquipamento=[];


  }


   ionViewDidLoad() {
    console.log('ionViewDidLoad cargarEquipamento');
    this.cargarEquipamento();

  }

   cargarEquipamento() {
    this.apiProvider.getEquipamento()
      .then(data => {
       console.log(data);
       this.dataEquipamento = data.data;
      });
 

     }
}



